import CustomColumn from '@/components/common/CustomColumn';
import SearchBox from '@/components/common/SearchBox';
// import multiDeviceComparison from '@/views/report/EquipmentDataComparison/multiDeviceComparison.vue';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  productionTaskEndGetListProductionTaskEndRecordQueryParameterAction,
  productionTaskEndGetPageProductionTaskEndRecordByConditionAction
} from '@/api/product';

import { searchCondetionFunc } from '@/utils/publicFunction';

export default {
  name: 'tabs',
  data() {
    return {
      CONSTANTS_PRODUCT: CONSTANTS_PRODUCT,
      VARIABLE: VARIABLE,
      // tab默认激活下标
      BTN_TEXT: BTN_TEXT,
      srearchList: [],
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      showData: {
        taskNumber: '1'
      },
      showFlag: true,
      // 显示那个表
      showTable: true,
      isSearchBoxShow: false,
      // 控制自定义列模态框显示
      dialogVisibleCustomColumn: false,
      dialogVisible: false,
      searchList: [],
      // table是否加载
      tableLoading: true,
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'endOperatorUserRealName',
          title: CONSTANTS_PRODUCT.END_USER,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'endTime',
          title: CONSTANTS_PRODUCT.END_TIME,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'groupName',
          title: CONSTANTS_PRODUCT.LINE_NAME,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'taskCode',
          title: CONSTANTS_PRODUCT.WORK_CODE,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'materialsName',
          title: CONSTANTS_PRODUCT.MATERIAL_NAME,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'allCountReport',
          title: CONSTANTS_PRODUCT.ALL_COUNT_REPORT,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'endTaskDeviceProductionRecordList',
          title: CONSTANTS_PRODUCT.USER_COMPUTE_PRODUCT,
          align: 'center',
          visible: true,
          minWidth: '120',
          slots: {
            default: ({ row, column }) => {
              var cellValue = row.endTaskDeviceProductionRecordList;
              var arr = [];
              for (var item in cellValue) {
                arr.push(<p>{'[' + cellValue[item].deviceName + '] ' + cellValue[item].recordCountReport}</p>);
              }
              return arr;
            }
          }
        },
        {
          field: 'endTaskDeviceProductionRecordList',
          title: CONSTANTS_PRODUCT.USER_END_RECORD,
          align: 'center',
          visible: true,
          minWidth: '120',
          slots: {
            default: ({ row, column }) => {
              var cellValue = row.endTaskDeviceProductionRecordList;
              var arr = [];
              for (var item in cellValue) {
                arr.push(<p>{'[' + cellValue[item].deviceName + '] ' + cellValue[item].manualRecordCount}</p>);
              }
              return arr;
            }
          }
        },
        {
          field: 'endTaskDeviceProductionRecordList',
          title: CONSTANTS_PRODUCT.USER_START_RECORD,
          align: 'center',
          visible: true,
          minWidth: '120',
          slots: {
            default: ({ row, column }) => {
              var cellValue = row.endTaskDeviceProductionRecordList;
              var arr = [];
              for (var item in cellValue) {
                arr.push(<p>{'[' + cellValue[item].deviceName + '] ' + cellValue[item].startManualRecordCount}</p>);
              }
              return arr;
            }
          }
        },
        {
          field: 'endTaskDeviceProductionRecordList',
          title: CONSTANTS_PRODUCT.DEV_COMPUTE_PRODUCT,
          align: 'center',
          visible: true,
          minWidth: '120',
          slots: {
            default: ({ row, column }) => {
              var cellValue = row.endTaskDeviceProductionRecordList;
              var arr = [];
              for (var item in cellValue) {
                arr.push(<p>{'[' + cellValue[item].deviceName + '] ' + cellValue[item].deviceRecordCountReport}</p>);
              }
              return arr;
            }
          }
        },
        {
          field: 'endTaskDeviceProductionRecordList',
          title: CONSTANTS_PRODUCT.DEV_END_RECORD,
          align: 'center',
          visible: true,
          minWidth: '120',
          slots: {
            default: ({ row, column }) => {
              var cellValue = row.endTaskDeviceProductionRecordList;
              var arr = [];
              for (var item in cellValue) {
                arr.push(<p>{'[' + cellValue[item].deviceName + '] ' + cellValue[item].autoRecordCount}</p>);
              }
              return arr;
            }
          }
        },
        {
          field: 'endTaskDeviceProductionRecordList',
          title: CONSTANTS_PRODUCT.DEV_START_RECORD,
          align: 'center',
          visible: true,
          minWidth: '120',
          slots: {
            default: ({ row, column }) => {
              var cellValue = row.endTaskDeviceProductionRecordList;
              var arr = [];
              for (var item in cellValue) {
                arr.push(<p>{'[' + cellValue[item].deviceName + '] ' + cellValue[item].startAutoRecordCount}</p>);
              }
              return arr;
            }
          }
        }
      ],

      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: []
    };
  },
  created() {
    this.findList();
    this.getSearchList();
  },
  methods: {
    /**
     * @description:获取查询条件
     * @param {type}
     * @return {type}
     * @author: CH
     */
    async getSearchList() {
      try {
        const res = await productionTaskEndGetListProductionTaskEndRecordQueryParameterAction({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // showSuccessMessage(res.message);
          // this.currentChangeRow = {};
          this.searchList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:查询数据
     * @param {type}
     * @return {type}
     * @author: CH
     */
    async findList() {
      try {
        // 显示table加载动画
        this.tableLoading = true;

        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };
        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: []
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }

        const res = await productionTaskEndGetPageProductionTaskEndRecordByConditionAction(params, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    exportTable() { },
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: cyb
     */
    showSearchCard() {
      // alert(1321);
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    /**
     * @description:返回
     * @param {type}
     * @return {type}
     * @author: CH
     */
    back() {
      this.$emit('historicalRecords');
    },
    /**
     * @description:处理查询数据
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.searchData = [];
      if (conditionArr1 !== undefined) {
        this.searchData.conditionArr1 = JSON.parse(
          JSON.stringify(conditionArr1)
        );
        for (var i in this.searchData.conditionArr1) {
          this.searchData.conditionArr1[
            i
          ].value = this.searchData.conditionArr1[i].value.toString();
        }
      }
      if (groupOperator !== undefined) {
        this.searchData.groupOperator = groupOperator;
      }
      if (conditionArr2 !== undefined) {
        this.searchData.conditionArr2 = JSON.parse(
          JSON.stringify(conditionArr2)
        );
        for (var x in this.searchData.conditionArr2) {
          this.searchData.conditionArr2[
            x
          ].value = this.searchData.conditionArr2[x].value.toString();
        }
      }
      this.onTablePageChange(1, this.tablePage.pageSize);
    },
    refreshTable() {
      this.findList();
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.tablePage.currentPage = 1;
      }
      this.findList();
    }
  },
  components: {
    // 加载未审核组件
    HeaderTitle,
    HeadOperationItem,
    VXETable,
    CustomColumn,
    SearchBox
    // multiDeviceComparison
  }
};
