// 引入组件
// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import {
  STATUS_CODE,
  TITLE,
  MSG,
  VARIABLE,
  BTN_TEXT,
  CONSTANTS_PRODUCT,
  EXPORT_NAME
} from '@/utils/constantsFile';
import CustomColumn from '@/components/common/CustomColumn';
import SearchBox from '@/components/common/SearchBox';
import historicalRecords from '@/views/production/productionFinish/historicalRecords.vue';
// import multiDeviceComparison from '@/views/report/EquipmentDataComparison/multiDeviceComparison.vue';
// 引入提示组件
import { showErrorMessage, showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  productionTaskGetPageProductionTaskEndByConditionAction,
  productionTaskEndGetListProductionTaskEndQueryParameterAction,
  productionTaskEndGetProductionTaskStartRecordByProductionIdAction,
  productionTaskEndInsertProductionTaskEndRecordAction,
  deviceRealProductionGetDeviceRealProductionByDeviceCode,
  deviceRealProductionGetListByGroupIdAction
} from '@/api/product';
import {
  exportExportProductionTaskEndAction
} from '@/api/export';

import DialogSettingColumn from '@/components/common/DialogCustomizeColumns/DialogSettingColumn';
import {
  // 保存自定义列配置
  saveCustomColumnSettingAPI,
  // 获取自定义列配置
  getCustomColumnSettingAPI
} from '@/api/customizeColumn';
import { getUrlKey } from '@/utils/common';
import { mixDefaultAndCustomColumns, searchCondetionFunc } from '@/utils/publicFunction';

export default {
  name: 'productionFinish',
  data() {
    var positiveNumber = (rule, value, callback) => {
      var x = /^\d+(?=\.{0,1}\d+$|$)/;// 正数
      if (!x.test(value)) {
        return callback(new Error('请输入正数'));
      } else if (Number(value) > 99999999 || Number(value) < 0) {
        return callback(new Error('请输入范围0-99999999的数字'));
      } else if (isNaN(value)) {
        return callback(new Error('请输入正数'));
      } else {
        if (this.showData.lineDevProduct[rule.field.split('.')[1]].manualRecordCount > value) {
          return callback(new Error('请大于开工数'));
        } else {
          return callback();
        }
      }
    };

    return {
      EXPORT_NAME: EXPORT_NAME,
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_PRODUCT: CONSTANTS_PRODUCT,
      srearchList: [],
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      formRow: {},
      showData: {
        lineDevProduct: []
      },
      showFlag: true,
      // 显示那个表
      showTable: true,
      isSearchBoxShow: false,
      searchList: [],
      searchData: [],
      // 控制自定义列模态框显示
      dialogVisibleCustomColumn: false,
      dialogVisible: false,
      // table是否加载
      tableLoading: true,
      // table数据
      rulesUpdate: {
        value: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { validator: positiveNumber },
          { pattern: /^[0-9.]+$/, message: '请输入正数' }
        ]
      },
      tableDatas: [],
      tableColumns: [],
      // table表头配置
      tableColumns_old: [
        {
          field: 'groupName',
          title: CONSTANTS_PRODUCT.LINE_NAME,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'deviceName',
          title: '设备名称',
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'taskCode',
          title: CONSTANTS_PRODUCT.WORK_CODE,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'materialsCode',
          title: CONSTANTS_PRODUCT.MATERIAL_CODE,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'materialsName',
          title: CONSTANTS_PRODUCT.MATERIAL_NAME,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'materialsModel',
          title: CONSTANTS_PRODUCT.MATERIAL_NORMS,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'modelNumber',
          title: CONSTANTS_PRODUCT.MODULE_NUM,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'taskCount',
          title: CONSTANTS_PRODUCT.WORK_NUM,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'completeCount',
          title: CONSTANTS_PRODUCT.FINISH_NUM,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'ngCount',
          title: CONSTANTS_PRODUCT.NG_NUM,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'inspectCount',
          title: CONSTANTS_PRODUCT.INSPECT_NUM,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '100',
          align: 'center',
          fixed: 'right',
          slots: {
            header: ({ column }) => {
              return [
                <span>
                  <span>{column.title}</span>
                  <i
                    onClick={() => this.showCustomColumn()}
                    class="el-icon-s-tools set-btn"
                  ></i>
                </span>
              ];
            },
            default: ({ row, column, cellValue }) => {
              return [
                <div>
                  <el-button type="text" onClick={() => this.goInfo(row)}>
                    完工
                  </el-button>
                </div>
              ];
            }
          }
        }
      ],

      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      // 保存自定义之后的列配置
      savedCustomColumns: [],
      // 是否显示自定义列对话框
      isSettingCustomDialogShow: false,
      tableColumns_old2: [],
      // 表头前部分
      tableColumns_head: [],
      // 表头操作部分
      tableColumns_operation: [],
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: [],
      deviceName: ''
    };
  },
  created() {
    // 加载扩展字段
    this.setTableColume();
    // 调用设备列表查询
    this.findList();
    this.getSearchList();
  },
  computed: {
    // 表格使用的表头配置
    myTableColumns: function() {
      if ((Array.isArray(this.savedCustomColumns)) && this.savedCustomColumns.length > 0) {
        console.log(this.savedCustomColumns);
        return this.savedCustomColumns;
      } else {
        return this.tableColumns_old2;
      }
    },
    getFinishAll() {
      var num = 0;
      for (var x in this.showData.lineDevProduct) {
        num = this.$options.filters.computedAdd(num, Number(this.showData.lineDevProduct[x].computeFinish));
      }
      return num;
    },
    getDevFinish() {
      return (index) => {
        if (this.showData.lineDevProduct[index].value > this.showData.lineDevProduct[index].manualRecordCount) {
          var num = this.$options.filters.accSubtr(Number(this.showData.lineDevProduct[index].value), Number(this.showData.lineDevProduct[index].manualRecordCount));
          this.showData.lineDevProduct[index].computeFinish = num;
          return num;
        } else {
          var numx = Number(this.showData.lineDevProduct[index].value);
          this.showData.lineDevProduct[index].computeFinish = numx;
          return numx;
        }
      };
    }
  },
  methods: {
    resetDevName() {
      this.deviceName = '';
      this.findList();
    },
    /**
     * @description:获取查询条件
     * @param {type}
     * @return {type}
     * @author: CH
     */
    async getSearchList() {
      try {
        const res = await productionTaskEndGetListProductionTaskEndQueryParameterAction({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // showSuccessMessage(res.message);
          // this.currentChangeRow = {};
          this.searchList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:查询数据
     * @param {type}
     * @return {type}
     * @author: CH
     */
    async findList() {
      try {
        // 显示table加载动画
        this.tableLoading = true;

        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };
        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: []
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }

        if (this.deviceName !== '') {
          conditionJson.conditionJson1.push({
            column: 'deviceName',
            ptype: 'string',
            type: 'like',
            value: this.deviceName
          });
        }

        const res = await productionTaskGetPageProductionTaskEndByConditionAction(params, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:完工
     * @param {type}
     * @return {type}
     * @author: CH
     */
    goInfo(row) {
      this.dialogVisible = !this.dialogVisible;
      this.formRow = JSON.parse(JSON.stringify(row));
      this.prevStart(row);
    },
    /**
     * @description: 获取上一次开工数据
     * @return: 无
     * @author: CH
     */
    async prevStart(row) {
      try {
        const res = await productionTaskEndGetProductionTaskStartRecordByProductionIdAction({
          productionTaskId: row.productionTaskId
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.showData.lineDevProduct = res.rows.map((item) => {
            item.value = 0;
            item.devAutoCount = 0;
            item.deviceName = '';
            item.computeFinish = 0;
            return item;
          });
          this.getLineDevProduct(row);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 根据分组id查询这个分组下的所有的设备为计数设备的实时产量
     * @return: 无
     * @author: CH
     */
    async getLineDevProduct(param) {
      try {
        if (param.assignType === 1) {
          const res = await deviceRealProductionGetListByGroupIdAction({
            groupId: param.groupId
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            for (var x in this.showData.lineDevProduct) {
              for (var y in res.rows) {
                if (this.showData.lineDevProduct[x].deviceCode === res.rows[y].deviceCode) {
                  this.showData.lineDevProduct[x].devAutoCount = res.rows[y].panelValue;
                  this.showData.lineDevProduct[x].deviceName = res.rows[y].deviceName;
                  // this.showData.lineDevProduct[x].value = res.rows[y].panelValue - this.showData.lineDevProduct[x].autoRecordCount + this.showData.lineDevProduct[0].manualRecordCount;
                  if (res.rows[y].panelValue - this.showData.lineDevProduct[x].autoRecordCount + this.showData.lineDevProduct[x].manualRecordCount <= 0) {
                    this.showData.lineDevProduct[x].value = 0;
                  } else {
                    this.showData.lineDevProduct[x].value = res.rows[y].panelValue - this.showData.lineDevProduct[x].autoRecordCount + this.showData.lineDevProduct[x].manualRecordCount;
                  }
                  // this.showData.lineDevProduct[x].value = res.rows[y].panelValue - this.showData.lineDevProduct[x].autoRecordCount + this.showData.lineDevProduct[0].manualRecordCount;
                }
              }
            }
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } else {
          const res = await deviceRealProductionGetDeviceRealProductionByDeviceCode({
            deviceCode: param.deviceCode
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.showData.lineDevProduct[0].devAutoCount = res.rows.panelValue;
            this.showData.lineDevProduct[0].deviceName = res.rows.deviceName;
            // this.showData.lineDevProduct[0].value = res.rows.panelValue - this.showData.lineDevProduct[0].autoRecordCount + this.showData.lineDevProduct[0].manualRecordCount;
            if (res.rows.panelValue - this.showData.lineDevProduct[0].autoRecordCount + this.showData.lineDevProduct[0].manualRecordCount <= 0) {
              this.showData.lineDevProduct[0].value = 0;
            } else {
              this.showData.lineDevProduct[0].value = res.rows.panelValue - this.showData.lineDevProduct[0].autoRecordCount + this.showData.lineDevProduct[0].manualRecordCount;
            }
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      try {
        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: []
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }
        const res = await exportExportProductionTaskEndAction({}, conditionJson);
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          this.$options.filters.setExportFunc(res, EXPORT_NAME.TASK_END_LIST + '.xls');
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 切换显示自定义列模态框
     * @param 无
     * @return: void
     * @author: ZJM
     */
    showCustomColumn() {
      // this.dialogVisibleCustomColumn = !this.dialogVisibleCustomColumn;
      this.isSettingCustomDialogShow = !this.isSettingCustomDialogShow;
    },
    /**
     * @description: 保存自定义列
     * @param 无
     * @return: void
     * @author: ZJM
     */
    saveCustomColumn(columns) {
      this.tableColumns = columns;
      this.showCustomColumn();
    },
    /**
     * @description:查看操作记录
     * @param {type}
     * @return {type}
     * @author: CH
     */
    historicalRecords() {
      this.showFlag = !this.showFlag;
    },
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: cyb
     */
    showSearchCard() {
      // alert(1321);
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    /**
     * @description: 恢复默认自定义列
     * @param 无
     * @return: void
     * @author: ZJM
     */
    resetCustomColumn() {
      this.showCustomColumn();
      // 按时间从大到小
      this.tableColumns = this.tableColumns.sort((a, b) => {
        return a.zIndex > b.zIndex ? 1 : -1;
      });
      this.tableColumns = this.tableColumns.map(item => {
        item.width = item.yWidth;
        item.visible = item.yVisible;
        return item;
      });
    },
    /**
     * @description:处理查询数据
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.searchData = [];
      if (conditionArr1 !== undefined) {
        this.searchData.conditionArr1 = JSON.parse(
          JSON.stringify(conditionArr1)
        );
        for (var i in this.searchData.conditionArr1) {
          this.searchData.conditionArr1[
            i
          ].value = this.searchData.conditionArr1[i].value.toString();
        }
      }
      if (groupOperator !== undefined) {
        this.searchData.groupOperator = groupOperator;
      }
      if (conditionArr2 !== undefined) {
        this.searchData.conditionArr2 = JSON.parse(
          JSON.stringify(conditionArr2)
        );
        for (var x in this.searchData.conditionArr2) {
          this.searchData.conditionArr2[
            x
          ].value = this.searchData.conditionArr2[x].value.toString();
        }
      }
      this.onTablePageChange(1, this.tablePage.pageSize);
    },
    /**
     * @description: 提交修改
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    submitStart() {
      this.$refs.productForm.validate((valid) => {
        // 若验证通过
        if (valid) {
          this.submitUpdateFunc();
        }
      });
    },
    async submitUpdateFunc() {
      try {
        const form = {};
        form.productionTaskId = this.formRow.productionTaskId;
        form.inspectCount = this.formRow.inspectCount;
        form.ngCount = this.formRow.ngCount;
        form.completeCount = this.formRow.completeCount;
        form.taskStartId = this.showData.lineDevProduct[0].taskStartId;
        var startTaskDeviceProductionRecordList = [];
        for (var item in this.showData.lineDevProduct) {
          var json = {};
          json.taskStartRecordId = this.showData.lineDevProduct[item].taskStartRecordId;
          json.deviceCode = this.showData.lineDevProduct[item].deviceCode;
          json.autoRecordCount = this.showData.lineDevProduct[item].devAutoCount;
          json.manualRecordCount = this.showData.lineDevProduct[item].value;
          json.recordCountReport = this.showData.lineDevProduct[item].computeFinish;
          startTaskDeviceProductionRecordList.push(json);
        }
        const res = await productionTaskEndInsertProductionTaskEndRecordAction(form,
          startTaskDeviceProductionRecordList);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 提示正常信息
          showSuccessMessage(res.message);
          this.dialogVisible = false;
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    refreshTable() {
      this.findList();
    },
    /**
     * @description: 显示扩展字段列表头
     * @return: void
     * @author: ZJM
     */
    async setTableColume() {
      var that = this;

      this.tableColumns = [];
      this.tableColumns_head = this.tableColumns_old.slice(
        0,
        this.tableColumns_old.length - 1
      );
      this.tableColumns_operation = this.tableColumns_old[
        this.tableColumns_old.length - 1
      ];
      this.tableColumns = this.tableColumns.concat(this.tableColumns_head);
      this.tableColumns = this.tableColumns.concat(
        this.tableColumns_operation
      );
      this.tableColumns_old2 = this.tableColumns;
      console.log(this.tableColumns_old2);
      setTimeout(() => {
        that.getCustomColumn(1);
      }, 0);
    },
    /**
     * @description: 获取自定义列配置
     * @param 无
     * @return void
     * @author: cyb
     */
    async getCustomColumn(cssType) {
      try {
        // 调接口获取当前页面的menuId
        // const urlInfo = await getMenuIdForUrlAPI({ url: this.$route.path });
        const id = getUrlKey('id');
        // 获取用户名
        const account = window.localStorage.getItem('account').replace(/"/g, '');
        const query = {
          account,
          cssType: cssType,
          menuId: id
        };
        const res = await getCustomColumnSettingAPI(query);
        // 如果有返回配置，则将默认表头和自定义配置混合
        if (Array.isArray(res) && res.length > 0) {
          const arr = mixDefaultAndCustomColumns(res, this.tableColumns_old2);
          if (this.savedCustomColumns.length > 0) {
            this.savedCustomColumns.splice(0);
          }
          this.savedCustomColumns.push(...arr);
        }
      } catch (error) {
        // showErrorMessage(error);
      }
    },
    /**
     * @description: (自定义列对话框的自定义事件) 点击“保存”按钮，调用接口
     * @param {number} cssType 同一路由下，区分不同页面的标志
     * @return promise
     * @author: cyb
     */
    async handleSaveColumnSetting(submitColumnSetting, cssType = this.cssType) {
      const loading = this.$loading({
        body: true,
        lock: true,
        text: '自定列配置提交中...'
      });

      try {
        // 获取路由的id
        // const { id } = await getMenuIdForUrlAPI({ url: this.$route.path });
        const id = getUrlKey('id');

        // 获取用户名
        const account = window.localStorage.getItem('account').replace(/"/g, '');
        const header = {
          account,
          cssType: 1,
          menuId: id
        };

        const res = await saveCustomColumnSettingAPI(
          header,
          submitColumnSetting
        );

        if (res.code === '0000') {
          loading.close();
          showSuccessMessage(res.message);
          this.refreshTableColumns();
        } else {
          loading.close();
          showErrorMessage(res.message || '操作失败！');
        }
      } catch (error) {
        loading.close();
        showErrorMessage(error + '666');
      } finally {
        // 关闭对话框
        this.isSettingCustomDialogShow = false;
      }
    },
    /**
     * @description: 刷新表格表头和表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableColumns() {
      this.getCustomColumn(1);
      // 刷新表格
      this.refreshTableData();
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.tablePage.currentPage = 1;
      }
      this.findList();
    }
  },
  components: {
    // 加载未审核组件
    HeaderTitle,
    HeadOperationItem,
    VXETable,
    CustomColumn,
    SearchBox,
    historicalRecords,
    DialogSettingColumn
    // multiDeviceComparison
  }
};
